.location-box-wrapper {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.location-suggestions {
  position: absolute;
  background: rgba(250, 250, 250, .9);
  color: #000;
  width: 100%;
  font-weight: 600;
}

.location-suggestions > div {
  border-bottom: solid 1px #000;
  padding: .75em 1em;
}

.location-box {
  background: rgba(90, 90, 90, .8);
  border-bottom: solid 1px rgba(0, 0, 0, .3);
  max-height: 100vh;
  font-size: 1.5em;
  padding: .5em .5em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.location-box.editing {
  background: rgba(255, 255, 255, .9);
  color: #000;
  border-bottom: solid 1px #000;
}

.location-box-wrapper.night > .location-box {
  background: rgba(0, 0, 0, .9);
  color: #fff;
  border-bottom: solid 1px rgba(255, 255, 255, .9);
}

.location-box-wrapper.night > .location-box.editing {
  background: rgba(255, 255, 255, .9);
  color: #000;
}

.location-icon > img {
  height: 1.5em;
}

.location-string {
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.location-search-field {
  background: transparent;
  border: 0;
  font-size: 1em;
  -ms-flex-positive: 1;
      flex-grow: 1;
  padding: 0;
  margin: 0;
  outline: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-negative: 1;
      flex-shrink: 1;
  color: inherit;
}


