.weather-container {
  background: transparent;
  color: #fff;
  -webkit-transition: background 0.5s ease;
  -o-transition: background 0.5s ease;
  transition: background 0.5s ease;
  min-height: 100vh;
}

@media only screen and (min-width : 1200px) {
	.weather-forecasts {
		display: -ms-flexbox;
		display: flex;
	}
}

/* Night and Day */

.night-bg {
  background: #001942;
}

.night-color {
  color: #001942;
}

.day-bg {
  background: #68b1ff;
}

.day-color {
  color: #68b1ff;
}


