.forecast-weather {
}

.forecast-weather-day {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: start;
      justify-content: flex-start;
  padding: .25em .75em;
  font-size: 1.5em;
}

.forecast-weather-day:nth-child(2n+1) {
  background: rgba(255, 255, 255, .15);
}

.forecast-temperature, .forecast-day-description, .forecast-day-icon, .forecast-weekday {
  margin: 0 .25em;
}

.forecast-temperature {
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -ms-flex-preferred-size: 15%;
      flex-basis: 15%;
}

.forecast-day-description {
  -ms-flex-positive: 1;
      flex-grow: 1;
  font-size: .9em;
}

.forecast-day-icon {
  -ms-flex-preferred-size: 15%;
      flex-basis: 15%;
  -ms-flex-positive: 0;
      flex-grow: 0;
  /*background: pink;*/
}

.forecast-weekday {
  -ms-flex-preferred-size: 15%;
      flex-basis: 15%;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

.forecast-day-icon > img {
  height: 2em;
}

@media only screen and (min-width : 600px) {
  .forecast-weather {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }

  .forecast-weather-day {
    background: rgba(255, 255, 255, .01);
    -ms-flex-preferred-size: 25%;
        flex-basis: 25%;
    padding: 0;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: stretch;
        align-items: stretch;
    -ms-flex-pack: start;
        justify-content: flex-start;
  }

  .forecast-temperature, .forecast-day-description, .forecast-day-icon, .forecast-weekday {
    padding: 0 .25em;
  }

  .forecast-day-description {
    -ms-flex-positive: 1;
        flex-grow: 1;
    font-size: 1em;
    margin: .25em 0;
  }

  .forecast-day-icon {    
    -ms-flex-preferred-size: auto;    
        flex-basis: auto;
    text-align: center;
    margin: .25em 0;
  }

  .forecast-day-icon > img {
    width: 50%;
    height: 100%;
  }

  .forecast-weekday {
    background: rgba(0, 0, 0, .125);
    margin: 0px;
    font-size: 1.25em;
    -ms-flex-preferred-size: auto;
        flex-basis: auto;
  }

  .forecast-temperature {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-preferred-size: auto;
        flex-basis: auto;
    margin: .25em 0;
  }
}

@media only screen and (min-width : 900px) {
  .forecast-weather-day {
    -ms-flex-preferred-size: 33.33%;
        flex-basis: 33.33%;
  }
}

@media only screen and (min-width : 1200px) {
  .forecast-weather-day {
    -ms-flex-preferred-size: 25%;
        flex-basis: 25%;
  }
}

@media only screen and (min-width : 1800px) {
  .forecast-weather-day {
    -ms-flex-preferred-size: 12.5%;
        flex-basis: 12.5%;
  }
}


